<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="songname">
				<el-input v-model="ruleForm.songname" 
					placeholder="" clearable :disabled=" false  ||ro.songname"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="yinleleixing">
				<el-select v-model="ruleForm.yinleleixing" placeholder="" :disabled=" false  ||ro.yinleleixing" >
					<el-option
						v-for="(item,index) in yinleleixingOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="songfile">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="3"
					:multiple="true"
					:fileUrls="ruleForm.songfile?ruleForm.songfile:''"
					@change="songfileUploadChange"
					></file-upload>
			</el-form-item>  
			<el-form-item class="add-item" label="" prop="zhuanjimingcheng">
				<el-input v-model="ruleForm.zhuanjimingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.zhuanjimingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="singer">
				<el-input v-model="ruleForm.singer" 
					placeholder="" clearable :disabled=" false  ||ro.singer"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="chuangjianshijian">
				<el-date-picker
					:disabled=" false  ||ro.chuangjianshijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.chuangjianshijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="nicheng">
				<el-input v-model="ruleForm.nicheng" 
					placeholder="" clearable :disabled=" false  ||ro.nicheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="lyric">
				<editor 
					v-model="ruleForm.lyric" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					songname : false,
					yinleleixing : false,
					fengmian : false,
					songfile : false,
					zhuanjimingcheng : false,
					singer : false,
					lyric : false,
					chuangjianshijian : false,
					nicheng : false,
					sfsh : false,
					shhf : false,
					thumbsupnum : false,
					crazilynum : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					storeupnum : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					songname: '',
					yinleleixing: '',
					fengmian: '',
					songfile: '',
					zhuanjimingcheng: '',
					singer: '',
					lyric: '',
					chuangjianshijian: '',
					nicheng: '',
					thumbsupnum: '',
					crazilynum: '',
					clicktime: '',
					clicknum: '',
					discussnum: '',
					storeupnum: '',
				},
				yinleleixingOptions: [],


				rules: {
					songname: [
						{ required: true, message: '', trigger: 'blur' },
					],
					yinleleixing: [
						{ required: true, message: '', trigger: 'blur' },
					],
					fengmian: [
					],
					songfile: [
						{ required: true, message: '', trigger: 'blur' },
					],
					zhuanjimingcheng: [
					],
					singer: [
						{ required: true, message: '', trigger: 'blur' },
					],
					lyric: [
					],
					chuangjianshijian: [
					],
					nicheng: [
					],
					sfsh: [
					],
					shhf: [
					],
					thumbsupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					crazilynum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					clicktime: [
					],
					clicknum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					discussnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.chuangjianshijian = this.getCurDateTime()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='songname'){
							this.ruleForm.songname = obj[o];
							this.ro.songname = true;
							continue;
						}
						if(o=='yinleleixing'){
							this.ruleForm.yinleleixing = obj[o];
							this.ro.yinleleixing = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o].split(",")[0];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='songfile'){
							this.ruleForm.songfile = obj[o];
							this.ro.songfile = true;
							continue;
						}
						if(o=='zhuanjimingcheng'){
							this.ruleForm.zhuanjimingcheng = obj[o];
							this.ro.zhuanjimingcheng = true;
							continue;
						}
						if(o=='singer'){
							this.ruleForm.singer = obj[o];
							this.ro.singer = true;
							continue;
						}
						if(o=='lyric'){
							this.ruleForm.lyric = obj[o];
							this.ro.lyric = true;
							continue;
						}
						if(o=='chuangjianshijian'){
							this.ruleForm.chuangjianshijian = obj[o];
							this.ro.chuangjianshijian = true;
							continue;
						}
						if(o=='nicheng'){
							this.ruleForm.nicheng = obj[o];
							this.ro.nicheng = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.nicheng!=''&&json.nicheng) || json.nicheng==0){
							this.ruleForm.nicheng = json.nicheng;
							this.ro.nicheng = true;
						}
					}
				});
				this.$http.get('option/yinleleixing/yinleleixing', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.yinleleixingOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`gedanxinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`gedanxinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			songfileUploadChange(fileUrls) {
				this.ruleForm.songfile = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

